<template>
  <div class="expert-rating">
    <el-table v-show="project.projectClass === 1"
              :data="tableData"
              border
              style="width: 100%">
      <el-table-column prop="date"
                       label=""
                       align="center"
                       width="80">
      </el-table-column>
      <el-table-column prop="name"
                       align="center"
                       label=""
                       width="80">
      </el-table-column>
      <el-table-column prop="address"
                       width="250"
                       label="">
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <div v-for="item in scope.row.standard">
            <div class="item">{{item}}</div>
          </div>
        </template>
      </el-table-column>
      <el-table-column fixed="right"
                       label=""
                       width="100">
        <template slot-scope="scope">
          <el-input size="small"
                    type="number"
                    min="0"
                    @blur="dafen(scope.row)"
                    v-model="scope.row.rating"></el-input>
        </template>
      </el-table-column>
    </el-table>
    <el-table v-show="project.projectClass === 2"
              :data="tableData2"
              border
              style="width: 100%">
      <el-table-column prop="date"
                       label=""
                       align="center"
                       width="100">
      </el-table-column>
      <el-table-column prop="name"
                       align="center"
                       label=""
                       width="80">
      </el-table-column>
      <el-table-column prop="address"
                       width="250"
                       label="">
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <div v-for="item in scope.row.standard">
            <div class="item">{{item}}</div>
          </div>
        </template>
      </el-table-column>
      <el-table-column fixed="right"
                       label=""
                       width="100">
        <template slot-scope="scope">
          <el-input rules="rules"
                    size="small"
                    min="0"
                    type="number"
                    @keyup.native="scope.row.rating =  scope.row.rating.replace(/[^\d]/g, '')"
                    @blur="dafen(scope.row)"
                    v-model="scope.row.rating"></el-input>
        </template>
      </el-table-column>
    </el-table>
    <el-form label-position="left"
             class="form"
             label-width="80px">
      <el-form-item label="">
        <el-input type="textarea"
                  v-model="params.introduction"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary"
                   @click="onSubmit(1)"></el-button>
        <el-button type="primary"
                   @click="onSubmit(2)"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { grade, getExpertScore } from '@/api/api';
export default {
  data() {
    return {
      project: {},
      expertscore: {},
      params: {
        projectId: '',
        firstScore: '',
        secondScore: '',
        thirdScore: '',
        forthScore: '',
        introduction: ''
      },
      tableData: [{
        date: '',
        name: '30',
        address: '',
        standard: [', 25-30',
          ', 15-25',
          ',0-15'],
        rating: ''
      }, {
        date: '',
        name: '30',
        address: '',
        standard: ['///25-30',
          '///15-25',
          '///0-15'],
        rating: ''
      }, {
        date: '',
        name: '30',
        address: '',
        standard: ['25-30',
          '15-25',
          '0-15'],
        rating: ''

      }, {
        date: '',
        name: '10',
        address: '',
        standard: [',8-10',
          ',8-10',
          ',0-4'],
        rating: ''
      }],
      tableData2: [{
        date: '',
        name: '40',
        address: '',
        standard: ['30-40',
          ',15-30',
          ',0-15'],
        rating: ''
      }, {
        date: '',
        name: '10',
        address: '',
        standard: [',8-10',
          '4-8',
          ',0-4'],
        rating: ''

      }, {
        date: '',
        name: '40',
        address: '',
        standard: ['30-40',
          '15-30',
          '0-15'],
        rating: ''

      }, {
        date: '',
        name: '10',
        address: '',
        standard: [',8-10',
          ',4-8',
          ',0-4'],
        rating: ''

      }]
    }
  },
  methods: {
    dafen(value) {
      const val = value.rating;
      if (val) {
        if (val === '') {
          this.$message.error('');
        }
        if (val < 0) {
          this.$message.error('');
        }
        let rating = parseInt(value.rating);
        let name = parseInt(value.name)
        if (rating > name) {
          this.$message.error('');
        }
        if (rating % 1 !== 0) {
          this.$message.error('');
        }
      }
    },
    getProject() {
      let param = {
        projectId: this.params.projectId
      }
      getExpertScore(param).then(res => {
        if (res.result) {
          this.project = res.result[0].project;
          this.expertscore = res.result[0].expertscore;
          this.params.introduction = this.expertscore.introduction;
          if (this.project.projectClass === 1) {
            this.tableData[0].rating = this.expertscore.firstScore;
            this.tableData[1].rating = this.expertscore.secondScore;
            this.tableData[2].rating = this.expertscore.thirdScore;
            this.tableData[3].rating = this.expertscore.forthScore;
          } else {
            this.tableData2[0].rating = this.expertscore.firstScore;
            this.tableData2[1].rating = this.expertscore.secondScore;
            this.tableData2[2].rating = this.expertscore.thirdScore;
            this.tableData2[3].rating = this.expertscore.forthScore;
          }
        }
      })
    },
    // 
    onSubmit(value) {
      if (this.project.projectClass === 2) {
        this.params.firstScore = this.tableData2[0].rating;
        this.params.secondScore = this.tableData2[1].rating;
        this.params.thirdScore = this.tableData2[2].rating;
        this.params.forthScore = this.tableData2[3].rating;
      } else {
        this.params.firstScore = this.tableData[0].rating;
        this.params.secondScore = this.tableData[1].rating;
        this.params.thirdScore = this.tableData[2].rating;
        this.params.forthScore = this.tableData[3].rating;
      }
      this.params.juniorStatus = value;
      grade(this.params).then(res => {
        if (res.resultCode === '200') {
          this.$message.success('');
          this.$router.push('/chuping');
        }
      })
    }
  },
  mounted() {
    this.params.projectId = this.$route.query.id;
    this.getProject();
  }
}
</script>

<style lang='scss' scoped>
.expert-rating {
  padding: 40px 40px 0 40px;
  input[type="number"] {
    -moz-appearance: textfield;
  }
  input::-webkit-outer-spin-button,
  input::-webkit-inner-spin-button {
    -webkit-appearance: none !important;
    margin: 0;
  }
  .item {
    line-height: 28px;
    border-bottom: 1px solid #dfe6ec;
  }
  .form {
    width: 800px;
    margin: 30px 0;
  }
}
</style>




